body{
    margin-left: 2em;
    margin-right: 2em;
    margin-top: 2em;
    position: relative;
}
.container-all{
    background-color: rgb(248, 248, 248);
    margin-left: -2em;
    margin-right: -2em;
    margin-top: -2em;
    padding: 4em 2em 2em 2em;
}

/////////////

///////////

.bgcomplete{
    background-color: rgb(36, 36, 36);
    color: white;
    margin-top: -8.1em;
    z-index: 210;
    text-align: right;
    padding: 3em 2em 3em 2em;
    width: 2em;
    margin-left: 180vh;
}
///////////
.body-container{
    background-color: rgb(36, 36, 36);
    color: white;
    margin-top: 2em;
    margin-left: -2em;
    margin-right: -2em;
    padding: 2em 2em 2em 2em;
    z-index: 99;
}
.body-project-feature{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    border-radius: 0.3em;
    border: solid rgba(107, 107, 107) 0.075em;
    text-align: center;
    line-height: 90vh;
    justify-content: center;
    align-items: center;
    margin-top: 2em;
}

.body-project-regular{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0.8em;
    margin-top: 0.8em;
    margin-bottom: 6em;
}

.body-p1{
    display: grid;
    grid-template-columns: 1fr 1.02fr;
}
.p1b{
    width: 100%;
    border-radius: 0.31em 0 0 0.31em;
}
.p1-text{
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    line-height: 1em;
}
.p1t1{
    display: inline block;
    justify-content: center;
    margin-top: auto;
    margin-bottom: auto;
}
.p1t2{
    margin-top: 30%;
    text-align: center;
}
.i{
    size: 5em;
}
Link{
    font-weight: 200;
}
.p1t2 a{
    text-decoration: none;
    font-size: 8vh;
    color: white;
    font-weight: 200;
}
.p1t2 a:hover{
    color: rgb(0, 110, 229);
    border-radius: 0.05em;
    transition-duration: 0.4s;
    transition-delay: 0.1s;
}
.pro-arrow{
    width: 12%;
    margin-top: -30%;
}

.p2t2{
    text-align: center;
}
.p2t2 a{
    text-decoration: none;
    font-size: 7vh;
    color: white;
    font-weight: 200;
}
.p2t2 a:hover{
    color: rgb(0, 110, 229);
    border-radius: 0.05em;
    transition-duration: 0.4s;
    transition-delay: 0.1s;
}
.p2t2 p{
    text-decoration: none;
    font-size: 3em;
    color: white;
    font-weight: 200;
}

.body-pr{
    display: grid;
    grid-template-rows: 2fr 1fr 1fr;
    border-radius: 0.3em;
    border: solid rgba(107, 107, 107) 0.075em;
    height: 100vh;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.body-pr img{
    width: 100%;
    margin-top: -3px;
    border-radius: 0.32em 0.32em 0 0;
}

.skills{
    display: inline-flex;
    gap: 0.3em;
}
.skill{
    margin-left: auto;
    margin-right: auto;
    border-radius: 0.3em;
    border: solid white 0.75px;
    color: white;
    // background-color: rgba(255, 255, 255);
    // color: rgba(63,63,63);
    padding: 0.5em 1em 0.5em 1em;
    font-weight: 400;
}
////////////
.body-container2{
    background-color: rgb(255, 255, 255);
    color: rgba(63,63,63);
    margin-left: -4em;
    margin-right: -4em;
    margin-bottom: -2em;
    padding: 2em 4em 4em 4em;
}
.body-project-design{
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    row-gap: 0.8em;
}
.body-dsgn{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-radius: 0.3em;
    background-color: rgb(255, 255, 255);
    border: solid rgba(107, 107, 107) 0.075em;
    text-align: left;
    justify-content: center;
    align-items: center;
    height: 14em;
}
.design-go{
    display: block;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.design-go a{
    text-decoration: none;
    font-size: 7vh;
    color: rgba(63,63,63);
    font-weight: 200;
    margin-top: 2em;
}
.design-go a:hover{
    color: rgb(0, 110, 229);
    border-radius: 0.05em;
    transition-duration: 0.4s;
    transition-delay: 0.1s;
}
.body-arrow{
    width: 10%;
    margin-top: -30%;
}
.dsgn-icon{
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}
.dsgn-project{
    width: 40%;
}
.skill2{
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    border-radius: 50em;
    border: solid rgba(63,63,63) 0.75px ;
    border-radius: 0.3em;
    color: rgba(63,63,63);
    padding: 0.5em 1em 0.5em 1em;
}

.phone-wrapper{
    display: none;
}
///////////////////////////////////
/// 
@media (max-width:800px) {
.wrapper{
    display: none;
}
.phone-wrapper{
    display: block;
    color: rgb(36, 36, 36);
    margin: 2em 1em 1em 1em;
}
}