.container-footer{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    bottom: 0;
    margin: -4em -2em -2em -2em;
    padding: 6em 4em 4em 4em;
    background-color: rgb(35,35,35);
    color: white;
}
.footer-l{
    display: inline-block;
}
.footer-l #sub-p{
    margin-top: -1em;
}
.footer-r{
    border: solid white 0.075em;
    border-radius: 0em 0.5em 0.5em 0.5em;
    padding: 2em 2em 2em 4em;
    margin: -1em;
    align-items: center;
    margin-top: auto;
    margin-bottom: auto;
}
.footer-icon{
    font-size: 1.2em;
    text-align: left;
    justify-content: center;
    align-items: center;
    gap: 4em;
}
.footer-icon a{
    text-decoration: none;
    color: white;
}
.line-horizon{
    border-bottom: solid white 0.075em;
    margin: 0em 1em 1em 0em;
}


@media (max-width:800px) {
.container-footer{
    margin-top: 2em;
    padding: 4em 2em 2em 2em;
    align-items: center;
    justify-content: center;
}
.footer-r{
    margin: 1em 1em 0 1em;
    padding-top: 2em;
    padding-bottom: 2em;
}
.line-horizon{
    border-bottom: solid white 0.075em;
    margin: 0em -1em 1em 0em;
}
}