.container-Navbar{
    display: grid;
    overflow: hidden;
    grid-template-columns: 1fr 2fr;
    top: 0;
    margin: -1em -2em -2em -2em;
    padding: 1em 2em 1em 3em;
    color: white;
}
.Navbar-l{
    display: inline-flex;
}

.control-bar a{
    color: black;
    text-decoration: none;
}
.control-bar a:hover{
    color: white;
    background-color: rgb(0, 110, 229);
    border-radius: 0.3em;
    transition-duration: 0.4s;
    transition-delay: 0.05s;
    padding: 0.2em 0.8em 0.2em 0.8em;
}

.control-bar #top-link{
    gap: 5em;
    margin: 0 1em 0 1em;
    color: rgb(35,35,35);
}
.Navbar-r{
    display: none;
}

@media (max-width:800px) {
.container-Navbar{
    padding: 2em 1em 2em 2em;
    text-align: center;
}
}