body{
    font-family: 'Lexend Deca', sans-serif;
    font-weight: 300;
    color: rgb(63, 63, 63);
    background-color: rgb(253, 253, 253);
    overflow-x: hidden;
}
////////////

.nav-wrap{
    margin-bottom: 0.8em;
    gap: 0.8em;
    display: inline-flex;
}
.botton-1{
    line-height: 5em;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    border: solid rgb(63, 63, 63) 0.75px;
    border-radius: 50em;
    width: 5em;
    height: 5em;
    text-decoration: none;
}

///////////
.bg-container{
    position: relative;
    left: 0;
    top: 0;
}
.navbar_container{
    top: 1em;
    margin-bottom: 0.5em;
    width: 100%;
    height: 2em;
    line-height: 2em;
    position: fixed;
    z-index: 100;
}
.nav-compact{
    display: none;
}
.nav_front{
    margin-left: 2em;
    top: 0;
}
.homelogo{
    display: inline-flex;
}
.homelogo p{
    margin-left: -20em;
    opacity: 0;
}
.homelogo p:hover{
    width: 100%;
    display: flex;
    left: 0;
    border-radius: 50em;
    padding-left: 3em;
    opacity: 80;
    z-index: 1;
    transition-duration: 0.7s;
    transition-delay: 0.3s;
    text-decoration: none;
}
.homelogo img{
    width: 5%;
    z-index: 50;
}
.homelogo img:hover{
    width: 5%;
}
.nav_back{
    display: inline-flex;
    justify-content: center;
    align-content: center;
}
.nav_back a{
    text-decoration: none;
    // color: rgb(63, 63, 63);   
    color: white;
}
.aboutme-1:hover{
    background-color:rgb(138, 144, 151);
    border-radius: 50em;
    text-decoration: none;
    transition-duration: 0.7s;
    transition-delay: 0.3s;
}
.nav_back a:hover{
    color: white;
    background-color: rgb(0, 110, 229);
    padding-left: 0.5em;
    padding-right: 0.5em;
    border-radius: 0.5em;
    text-decoration: none;
    transition-duration: 0.7s;
    transition-delay: 0.3s;
}
.nav_float{
    display: inline-flex;
    gap: 6em;
    border-radius: 0.3em;
    // border: solid rgb(63,63,63) 0.75px;
    background-color: rgb(63,63,63);
    line-height: 1.7em;
    height: 1.7em;
    padding-left: 2.6em;
    padding-right: 2.6em;
}
.aboutme-1{
    width: 100%;
    display: flex;
    gap: 0.5em;
}
.logo {
    width: 2%;
    margin-bottom: 0.15em;
}
.banner_container{
    width: 100%;
}
.homebg{
    position: relative;
    width: 100%;
    top: 3em;
    z-index: 200;
}
#avatar{
    position: absolute;
    display: flex;
    width: 15.5%;
    top: 39.25%;
    left: 59%;
    z-index: 202;
    // animation: figure1 15s ease-in-out infinite alternate;
    // animation-fill-mode:  forwards;
}
.title-box{
    position: absolute;
    display: inline-block;
    word-wrap: break-word;
    width: 600px;
    top: 15vh;
    left: 7em;
    z-index: 201;
    color: rgb(63, 63, 63);
}
.ux-empha{
    display: flex;
    gap: 1em;
}
.resume-float{
    display: inline-flex;
    border-radius: 50em;
    border: none;
    background-color: rgb(0, 110, 229) ;
    color: white;
    line-height: 2em;
    height: 2em;
    margin-top: 2em;
    padding-left: 3em;
    padding-right: 3em;
    align-items: center;
}
.resume-button{
    width: 100px;
    display: flex;
    gap: 0.5em;
    padding: 0.1em;
}
.resume-button img{
    width: 100%;
}
.resume-float:hover{
    background-color: #143254;
    transition-duration: 0.7s;
    transition-delay: 0.3s;
}
.job-role{
    position: absolute;
    display: inline-block;
    word-wrap: break-word;
    top: 30vh;
}
.project-showcase{
    margin-left: 2em;
    margin-right: 2em;
}
.section_divider{
    margin-top: 10em;
    margin-bottom: 6em;
    text-align: center;
}
.project-box1{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1em;
    margin-bottom: 2.25em;
    background-color: rgb(255, 255, 255);
    height: 25em;
    // border: solid rgb(105, 105, 105) 0.5px;
    padding: 1em;
    border-radius: 1.5em;
    text-align: center;
}
.project-box2{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1em;
    margin-bottom: 2.25em;
    background-color: rgb(255, 255, 255);
    height: 25em;
    padding: 1em;
    border-radius: 1.5em;
    text-align: center;
}
.project-box1:hover{
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.4);
    transition-duration: 0.5s;
    transition-delay: 0.2s;
}
.project-roles{
    display: grid;
    grid-template-rows: 1fr, 1fr, 1fr, 1fr, 1fr;
    text-align: center;
    align-items: center;
}
.uprole{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    column-gap: 0.5em;
    row-gap: 0.5em;

}
.roles{
    background-color: rgba(255, 255, 255, 0.35);
    border-radius: 0.7em;
    align-items: center;
    justify-content: center;
    margin-top: auto;
    margin-bottom: auto;
    height: 4em;
    padding-top: 1.2em;
    font-weight: bolder;
}
.godiv{
    display: block;
    color: rgb(0, 110, 229);
    font-weight: bold;
    padding-top: 1em;
}
.godiv a:hover{
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    color: white;
    background-color: rgb(0, 110, 229);
    padding-top: 1em;
    padding-bottom: 1em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 50em;
    text-decoration: none;
    transition-duration: 0.4s;
    transition-delay: 0.1s;
}
.maintext{
    font-size: 5em;
    font-weight: bold;
    color:rgb(0, 110, 229);
    margin-left: -0.06em;
    line-height: 1.1em;
}
.UI-box{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 1em;
}
.compact-go{
    display: none;
}
.title-1{
    font-size: 5em;
}
#banner1{
    display: flex;
    width: 95%;
    justify-content: center;
    margin-top: -4.5em;
}
#banner2{
    display: flex;
    width: 95%;
    justify-content: center;
    margin-top: 4.5em;
}
#banner3{
    display: flex;
    width: 78%;
    justify-content: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
#banner4{
    display: flex;
    width: 50%;
    justify-content: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}
#banner5{
    display: flex;
    width: 80%;
    justify-content: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

@media (min-width:1700px) {
    .navbar_container{
        height: 3.5em;
    }
    .nav_back{
        text-align: center;
        justify-content: center;
        align-items: center;
        margin-top: auto;
        margin-bottom: auto;
    }
    .nav_front{
        margin-top: 0.5em;
    }
    .title-box{
        width: 1000px;
    }
    .title-box h5{
        font-size: 1.8em;
    }
    .maintext{
        font-size: 7em;
    }
    #banner1{
        width: 70%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
    #banner2{
        width: 70%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
    #banner3{
        width: 53%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
    #banner4{
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
        width: 40%;
    }
    #banner5{
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
        width: 70%;
    }

}

@media (max-width:800px) {
    .navbar_container{
        display: none;
    }
    .nav-compact{
        display: inline-flex;
        margin: -5em -2em -2em -2em;
        padding: 0.5em 0.5em em 0.5em;
        background-color: rgba(194, 194, 194, 0.25);
    }
    .nav-compact img{
        width: 8%;
    }
    .nav_front{
        display: none;
    }
    .nav_back{
        text-align: center;
        justify-content: center;
        align-items: center;
        margin-top: 0.3em;
    }
    .title-box{
        width: 170px;
        top: 6em;
        margin-left: -5em;
    }
    .title-box h5{
        font-size: 0.8em;
    }
    #tagline{
        display: none;
    }
    .maintext{
        font-size: 1.2em;
    }
    .title-1{
        font-size: 2em;
    }
    .resume-float{
        margin-top: -0.1em;
        margin-left: -3.2em;
        scale: 0.5;
        text-align: center;
        justify-content: center;
        align-items: center;
    }
    .section_divider{
        margin-top: 5em;
        margin-bottom: 1em;
    }
    .project-box1{
        height: 115px;
        border-radius: 0.7em;
    }
    .project-box1 p{
        display: none;
    }
    .project-box2{
        display: block;
        height: 15em;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
    }
    .project-box2 Link{
        text-align: center;
    }
    .project-box2 p{
        display: none;
    }
    .project-roles h5{
        display: none;
    }
    .uprole{
        display: none;
    }
    .project-title img{
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
    }
    #banner1{
        width: 100%;
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
    }
    #banner2{
        justify-content: center;
        margin-left: -30px;
        margin-top: auto;
        margin-bottom: auto;
        width: 120%;
    }
    #banner3{
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
        width: 100%;
    }
    .compact-go{
        display: block;
    }
    .godiv{
        justify-content: center;
        text-align: center;
        margin-top: auto;
        margin-bottom: auto;
        margin-left: auto;
        margin-right: auto;
    }
    #avatar{
        top: 54%;
    }
}