h1{
    font-size: 2em;
}
.container-main{
    color: rgb(35,35,35);
    overflow: hidden;
    margin: -3em -2em -2em -2em;
    padding: 4em 4em 4em 4em;
}
////////--------------TITLE-HEADER--------------////////

.header-content-wrapper{
    display: grid;
    grid-template-columns: 1.5fr 0.1fr 1fr 0.1fr 4fr 0.1fr 2fr;
    border-radius: 0.3em;
    // border: solid rgba(107, 107, 107) 0.075em;
    text-align: center;
    line-height: 88px;
    justify-content: center;
    align-items: center;
}
.ver-line{
    border-left: solid rgba(107, 107, 107) 0.2em ;
    align-items: center;
    justify-content: center;
    height: 80%;
}
.header-content1{
    text-align: left;
    display: inline-block;
    line-height: 10px;
    justify-items: center;
    transform: translateY(1%);
}
.header-content2{
    text-align: left;
    display: inline-block;
    line-height: 10px;
    justify-items: center;
    transform: translateY(8%);
}
.logo-main{
    text-align: center;
    margin-left: 3.7vw;
}
.phone-display{
    display: none;
}

////////--------------BACKGROUND-HEADER--------------////////

.intro-container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2em;
    margin-top: -5em;
    margin-bottom: 2em;
    text-align: left;
    justify-content: center;
    align-items: center;
    padding: 4vh;
}
.intro-title{
    top: 50%;
}
.intro-resume {
    text-align: right;
    margin-top: 30%;
}
.homebg{
    position: relative;
    margin-top: 0em;
    z-index: 1;
}
.cloudbg{
    position: absolute;
    z-index: 1;
    margin-top: 3.5em;
    width: 100%;
    animation: cloud 60s linear infinite  ;
}
  @keyframes cloud  {
    from {
        margin-left: 0%;
    } to {
        margin-left: 80%;
    }
  }

.me-icon{
    z-index: 100;
    position: absolute;
    width: 13.5%;
    right: 10.1%;
    top: 13.2%;
}
.skill-panel{
    position: absolute;
    display: inline-flex;
    width: 2.5em;
    top: 20em;
    left: 2em;
    text-align: left;
    z-index: 101;
}
.skill-panel .hidden-sk{
    visibility: hidden;
    margin-left: 1em;
    line-height: 2em;
    white-space: pre-line;
}
.hidden-sk p{
    background-color: #FEC20D;
    padding: 0.025em 0.5em 0.025em 0.5em;
    border-radius: 0.3em;
    text-align: center;
}
.skill-panel:hover .hidden-sk {
    visibility: visible;
    transition-duration: 0.4s;
    transition-delay: 0.1s;
}
.skill-panel h3{
    margin-top: -0.16em;
}

////////--------------UX PROJECTS--------------////////

.body-ux{
    background-color: rgb(35,35,35);
    color: white;
    margin-top: 8em;
    margin-left: -4em;
    margin-right: -4em;
    padding-left: 4em;
    padding-right: 4em;
    padding-bottom: 4em;
}
.ux-card{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.p1t1{
    display: inline block;
    justify-content: center;
    margin-top: auto;
    margin-bottom: auto;
}
.phone-body{
    display: none;
}
.phone-design-card{
    display: none;
}

////////--------------PHONE--------------////////

@media (max-width:800px) {
    .header-content-wrapper{
        display: none;
    }
    .phone-display{
        overflow: hidden;
        display: block;
        margin: -2em;
        margin-bottom: 3em;
        text-align: center;
    }
    .line-horizon-1{
        border-bottom: solid rgb(35,35,35) 0.075em;
    }
    .line-horizon-2{
        border-bottom: solid white 0.075em;
    }
    .background-story{
        display: none;
    }
    .phone-second-row{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin-top: 1.5em;
        background-color: #dddddd;
        padding: 1em 1em 1em 1em;
    }
    .skill-list{
        display: inline-block;
        text-align: left;
        z-index: 200;
        line-height: 1em;
        white-space: pre-line;
        margin-top: 1em;
    }
    .me-icon{
        top: 57%;
        width: 40%;
    }
    .round-shape{
        background-color: #b1b1b1;
        width: 5em;
        padding: 3em;
        border-radius: 50em;
        margin-top: 5.2em;
        margin-left: 1.6em;
    }
    .body-ux{
        display: none;
    }
    .phone-body{
        display: block;
        background-color: rgb(35,35,35);
        color: white;
        margin-left: -4em;
        margin-right: -4em;
        padding: 4em;
        text-align: center;
        margin-bottom: -4em;
    }
    .phone-design-card{
        display: none;
        text-align: center;
        margin-top: 2em;
        margin-bottom: 2em;
    }
    }